
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'HelpCon',
components: {
XHeader
},
created() {
this.getData();
},
data() {
return {
con: {}
};
},
methods: {
async getData() {
let res = await this.$http('/queryArticleDetail', {
body: {
articleId: this.$route.params.id
}
});
console.log(res);
this.con = Object.assign({}, res.returnMap);
}
}
};



